* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background: #e9ebee;
    color: #1d2129;
}
.Navbar,.Footer {
    color: #ffffff;
    padding: 15px;
    font-size: 25px;
    background: #567ed0;
}
.Footer {
    position: relative;
    text-align: center;
    bottom: 0;
}
.Footer__info{
    color: whitesmoke;
}
.Input, .Post {
    border: 1px solid hsl(216, 8%, 88%);
    border-radius: 4px;
    width: 576px;
    margin: 20px auto;
    background-color: #ffffff;
}
.Input__header{
    background-color: #f5f6f7;
    border-bottom: 1px solid #dddfe2;
    border-radius: 2px 2px 0 0;
    margin: 0;
    padding: 15px;
}
.Input__field {
    width: 100%;
    height: 100px;
    padding: 10px;
    font-size: 20px;
    background-color: #fff;
    border: none;
  }
  
.Input__field:focus {
    outline: none;
  }
.Post {
    padding: 15px;
  }
  
.Post__image {
    width: 100%;
    margin-top: 15px;
  }
  
.Post__delete {
    border: none;
    background: none;
  
    padding: 0;
    float: right;
  
    cursor: pointer;
  }